/* HEADER */
.header {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 10;
  background: transparent;
  // transition: .25s $ease
  &__wrapper {
    display: flex;
    align-items: center;
    padding-top: 60px; }

  &__back {
    position: absolute;
    top: 50%;
    width: 30px;
    transform: translate3d(0,-50%,0);
    left: -35px;
    padding-right: 15px;
    color: $fontColor;
    font-size: 0px;
    cursor: pointer;
    opacity: 0;
    pointer-events: none;
    transition: transform .25s $ease;
    .ico {
      font-size: 24px; }
    &:hover {
      transform: translate3d(-7px,-50%,0); } }


  &__logo {
    // mind right padding for flex prop
    flex: 0 0 199px;
    position: relative;
    // posr is for header back
    font-size: 35px;
    padding-right: 30px;
    font-size: 0;
    svg {
      width: 100%;
      overflow: visible; }
    .logo-mark {
      // fill: url(#linearGradient-1)
      // transform-origin: right top
      // center should be set as BBox with JS
      transition: transform .35s easeInOutQuint; }
    &-link {
      position: relative;
      display: inline-block;
      &:hover {
        .logo-mark {
          transform: rotate(720deg); } } } }

  &__lang {
    margin-left: auto;
    font-size: 12px;
    text-transform: uppercase;
    a {
      position: relative;
      display: inline-block;
      margin-right: 20px;
      color: $colorGray;
      &::after {
        display: inline-block;
        content: ' ';
        position: absolute;
        bottom: 0;
        left: 0;
        right: 100%;
        height: 1px;
        background: currentColor;
        transition: .25s $ease; }

      &.is-active {
        font-weight: 900;
        color: $colorPrimary;
        pointer-events: none;
        &::after {
          right: 0; } }
      &:hover {
        &::after {
          right: 0; } } } }

  &__phone {
    margin-left: 30px; }

  &.is-white, &.is-blue {
    .header__logo {
      .logo-mark {
        fill: white; }
      .logo-letters {
        fill: white; } }
    .b-phone {
      &::after {
        box-shadow: 0 22px 22px 0 rgba(0,0,0,0.12); }
      &__numbers {
        &::after {
          box-shadow: 0 22px 22px 0 rgba(0,0,0,0.12); } } } }
  &.is-blue {
    .header__lang {
      a {
        color: rgba(white, .5);
        &.is-active {
          color: white; } } }

    .b-phone {
      &::after {
        background-image: linear-gradient(134deg, #3023AE 0%, #C86DD7 100%); } } }

  &.is-back-visible {
    .header__back {
      opacity: 1;
      pointer-events: all; } }

  &.is-hidden {
    visibility: hidden !important;
    pointer-events: none; }

  &.is-fixed {
    transform: translate3d(0,-100%,0);
    background-image: linear-gradient(134deg, #3023AE 0%, #8A4FC6 59%, #C86DD7 100%);
    box-shadow: 0 15px 15px 0 rgba(0,0,0,0.12);
    transition: .25s $ease;
    &.is-blue {
      background-image: linear-gradient(39deg, #3CAAC4 -10%, #57D4E9 59%, #A1F01F 120%); }
    &.is-black-fixed {
      background-image: linear-gradient(-127deg, #292A2C 0%, #28282A 100%); }
    .header__wrapper {
      padding-top: 24px;
      padding-bottom: 20px; }
    // copy styles for white shadow and logo
    .header__logo {
      flex: 0 0 169px;
      .logo-mark {
        fill: white; }
      .logo-letters {
        fill: white; } }
    .b-phone {
      &::after {
        box-shadow: 0 22px 22px 0 rgba(0,0,0,0.12); }
      &__numbers {
        &::after {
          box-shadow: 0 22px 22px 0 rgba(0,0,0,0.12); } } }
    .header__lang {
      a {
        color: rgba(white, .5);
        &.is-active {
          color: white; } } } }


  &.is-fixed-visible {
    position: fixed;
    transform: translate3d(0,0,0);
    &.is-scrolling-top {
      top: -75px; } }

  &.is-white, &.is-blue, &.is-fixed {
    .header__back {
      color: white; } } }

@include r($wide) {
  .header {
    &__back {
      left: -30px; } } }

@include r($desktop) {
  .header {
    &__wrapper {
 } } }      // padding-top: 40px

@include r($tablet) {
  .header {
    &__wrapper {} } }

@include r($mobile) {
  .header {
    &__lang {
      display: none; }
    &__phone {
      margin-left: auto; }
    &__back {
      left: -25px;
      .ico {
        font-size: 20px; } } } }

@include r(440) {
  .header {
    &__logo {
      // mind right padding for flex prop
      flex: 0 0 183px; } } }

@include r(375) {
  .header {
    &.is-back-visible {
      .header {
        &__logo {
          margin-left: 15px; } } } } }
